import * as React from "react";
import Lottie from "lottie-react";
import {useOnScreen} from "../../../hooks/IsInViewport";

const lottieOptions = {
    loop: true,
    autoplay: false,
    rendererSettings: {
        preserveAspectRatio: 'xMidYMid slice',
    }
};

export const LottieWrapper = (props) => {
    const containerRef = React.useRef(),
        lottieRef = React.useRef(),
        onScreen = useOnScreen(containerRef),
        { animationData, style } = props;

    React.useEffect(() => {
        if (animationData) {
            if (onScreen) {
                lottieRef.current.play();
            } else {
                lottieRef.current.pause();
            }
        }
    }, [animationData, onScreen]);

    const options = {
        animationData,
        ...{
            ...lottieOptions,
            autoplay: onScreen,
        },
    };

    return (
        <div ref={containerRef} style={style}>
            {animationData &&
                <Lottie
                    lottieRef={lottieRef}
                    className="LottieAnimation"
                    {...options}
                    style={style}
                    {...props}
                />
            }
        </div>
    );
};

export default LottieWrapper;