import React from "react";
import htmlAstScraper from "../../htmlAstScraper";
import { Link as GatsbyLink } from "gatsby";
import useMobileDetect from 'use-mobile-detect-hook';
import {
    Drawer,
    DrawerBody,
    DrawerOverlay,
    DrawerContent,
    DrawerCloseButton,
    IconButton,
    useDisclosure,
    Text,
} from '@chakra-ui/react';
import { HamburgerIcon } from '@chakra-ui/icons'

import "./styles.scss";

const DesktopNavBar = ({links}) => {
    return (
        <div className="Navigation">
            <nav className="NavBar">
                <ul>
                    {links.map((l, idx) => {
                        return (
                            <li key={idx}>
                                <GatsbyLink
                                    color='black.500'
                                    activeStyle={l.isActive ? { color: "#ffb6be", fontWeight: 'bold' } : {}}
                                    to={l.href}>
                                    {l.text}
                                </GatsbyLink>
                            </li>
                        );
                    })}
                </ul>
            </nav>
        </div>
    );
};

const MobileNavBar = ({links, hamburgerIconColor}) => {
    const { isOpen, onOpen, onClose } = useDisclosure();
    const btnRef = React.useRef();

    return (
        <nav>
            <IconButton
                colorScheme='white'
                icon={<HamburgerIcon color={hamburgerIconColor} />}
                ref={btnRef}
                onClick={onOpen}
                aria-label='Open Navigation'
            />

            <Drawer
                isOpen={isOpen}
                placement='left'
                onClose={onClose}
                finalFocusRef={btnRef}
            >
                <DrawerOverlay />
                <DrawerContent>

                    <DrawerBody>
                        <DrawerCloseButton />
                        <ul className="MobileNavBar">
                            {links.map((l, idx) => {
                                return (
                                    <li key={idx}>
                                        <GatsbyLink
                                            color='black.500'
                                            activeStyle={l.isActive ? { color: "#ffb6be", fontWeight: 'bold' } : {}}
                                            onClick={() => {
                                                onClose();
                                            }}
                                            to={l.href}>
                                                <Text fontSize="2xl">
                                                    {l.text}
                                                </Text>
                                        </GatsbyLink>
                                    </li>
                                );
                            })}
                        </ul>
                    </DrawerBody>
                </DrawerContent>
            </Drawer>
        </nav>
    );
};

export const NavBar = ({ curRoute, hamburgerIconColor = 'black.400', htmlAst }) => {
    const detectMobile = useMobileDetect();

    const set = new Set(['a']);
    const links = htmlAstScraper(htmlAst, set).filter(l => {
        return l.children.length > 0 && l.children[0].value;
    }).map(l => {
        const href = l.properties.href,
            normalizedHref = href.charAt(href.length - 1) === '/' ? href.slice(0, href.length - 1) : href;
        return {
            isActive: normalizedHref === curRoute,
            text: l.children[0].value,
            href: l.properties.href,
        };
    });

    if (detectMobile.isMobile()) {
        return (<MobileNavBar links={links} hamburgerIconColor={hamburgerIconColor} />);
    }

    return (<DesktopNavBar links={links} />);

};

export default NavBar;