import React, { useState, useEffect } from "react";
import BasicNavBar from "./basicNavBar";
// import sakuraAnimation from "../../../lottie/cherry-flowers.json";

import LottieWrapper from "../../components/LottieWrapper";
import { Text } from '@chakra-ui/react';
import cn from 'classnames';
import useMobileDetect from 'use-mobile-detect-hook';

import "@fontsource/dancing-script";
import "./styles.scss";

const mobileLottieContainerHeight = 200,
    desktopLottieContainerHeight = 300,
    desktopFontSizeMax = 100,
    desktopFontSizeMin = 60,
    mobileFontSizeMax = 60,
    mobileFontSizeMin = 30;

const CollapasingNavBar = ({ curRoute, htmlAst }) => {
    const detectMobile = useMobileDetect(),
        isMobile = detectMobile.isMobile();
    const [isShrunkState, setIsShrunk] = useState(false);

    useEffect(() => {
        const handler = () => {
            setIsShrunk((curIsShrunk) => {
                const offset = window.pageYOffset;
                const lottieHeight = (isMobile ? mobileLottieContainerHeight : desktopLottieContainerHeight);
                const shouldShrink = (offset + 16) > lottieHeight;
                if (!curIsShrunk && shouldShrink) {
                    window.scrollBy(0, 24);
                }

                return shouldShrink;
            });
        };

        window.addEventListener("scroll", handler);
        return () => window.removeEventListener("scroll", handler);
    }, [isMobile]);

    let fontSize = desktopFontSizeMax;
    if (isMobile) {
        fontSize = isShrunkState ? mobileFontSizeMin : mobileFontSizeMax;
    } else {
        fontSize = isShrunkState ? desktopFontSizeMin : desktopFontSizeMax;
    }

    return (
        <div className={cn("Navigation", { "IsShrunk": isShrunkState })}>
            <div className="NavigationHeader">
                <Text className="AAndM" style={{ fontSize: `${fontSize}px` }}>A + M</Text>
            </div>

            <BasicNavBar hamburgerIconColor={isShrunkState ? 'white.800' : 'pink.400'} curRoute={curRoute} htmlAst={htmlAst} />
        </div>
    );
};

export const NavBar = ({ curRoute, htmlAst }) => {
    const detectMobile = useMobileDetect();
    const [animationData, setAnimationData] = useState(null);

    useEffect(() => {
        import("../../../lottie/cherry-flowers.json").then(setAnimationData);
    }, []);

    const lottieOptions = {
        animationData,
        loop: true,
        autoplay: true,
        rendererSettings: {
            preserveAspectRatio: 'xMidYMid slice',
        }
    };

    return (
        <>
            <LottieWrapper
                {...lottieOptions}
                style={{
                    height: detectMobile.isMobile() ? mobileLottieContainerHeight : desktopLottieContainerHeight,
                    width: '100%'
                }}
            />

            <CollapasingNavBar curRoute={curRoute} htmlAst={htmlAst} />
        </>
    );
};

export default NavBar;