
const getNodes = (props, typeToScrape) => {
    if (typeof props !== 'object') {
        return [];
    }

    let nodes = [];

    if (props.children) {
        props.children.forEach((child) => {
            if (typeof child === 'object') {
                const type = typeof child.type === "function" ? child.type().type : child.type;

                if (typeToScrape.has(type) || typeToScrape.has(child.tagName)) {
                    nodes = [...nodes, child];
                } else {
                    const res = getNodes(child, typeToScrape);
                    nodes = [...nodes, ...res];
                }
            }
        });
    }

    return nodes;
}


export default (props, typeToScrape) => {
    return getNodes(props, typeToScrape).map(img => {
        const newProps = {...img.props};
        delete newProps.style;
        return {
            ...img,
            props: newProps
          };
    });
}
